<template>
  <div id="app">
    <div v-if="showDeleteModal" class="modal">
      <div class="modal-content">
        <span class="close" @click="closeDeleteModal">&times;</span>
        <p>你确定要删除这个车辆吗？</p>
        <button @click="confirmDelete">确认</button>
        <button @click="closeDeleteModal">取消</button>
      </div>
    </div>
    <div v-if="showModal" class="modal">
      <div class="modal-content">
        <span class="close" @click="closeModal">&times;</span>
        <form @submit.prevent="updateItem">
          <label>车辆名称</label>
          <input type="text" v-model="currentItem.name" required>
          <label>租赁价格</label>
          <input type="text" v-model="currentItem.price" required>
          <label>车辆类型</label>
          <input type="text" v-model="currentItem.type" required>
          <label>车辆描述</label>
          <textarea v-model="currentItem.description" required></textarea>
          <button type="submit">更新</button>
        </form>
      </div>
    </div>
    <div class="son">
      <!-- 搜索框 -->
      <input type="text" v-model="searchQuery" placeholder="搜索..." />
      <button @click="search">搜索</button>
      <!-- 表格 -->
      <table>
        <thead>
          <tr>
            <th></th>
            <th>序号</th>
            <th>车辆名称</th>
            <th>租赁价格</th>
            <th>车辆类型</th>
            <th>车辆描述</th>
            <th>车主</th>
            <th>车辆状态</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in searchResults" :key="index">
            <td><input type="checkbox" /></td>
            <td>{{ index + 1 }}</td>
            <td>{{ item.name }}</td>
            <td>{{ item.price }}</td>
            <td>{{ item.type }}</td>
            <td>{{ item.description }}</td>
            <td>{{ item.owner }}</td>
            <td>{{ item.status === 'AVAILABLE' ? '未租出' : '已租出' }}</td>
            <td>
              <button @click="editItem(index)">编辑</button>
              <button @click="deleteItem(index)">删除</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
  
<script>
import axios from 'axios';

export default {
  data() {
    return {
      cars: [],
      searchResults: [],
      searchQuery: '',
      showModal: false,
      currentItem: {},
      showDeleteModal: false,
    };
  },
  created() {
    this.updateData();
  },
  methods: {
    updateData(){
      axios.get('/car/getAllCars')
      .then(response => {
        this.cars = response.data;
        this.searchResults = this.cars.map(car => ({
          id: car.carID,
          name: car.carName,
          price: car.carPrice,
          type: car.carType,
          description: car.carDis,
          owner: car.carMaster,
          status: car.carState,
          renter: car.renter
        }));
        console.log(this.searchResults)
      })
      .catch(error => {
        console.error(error);
        console.log("error")
      });
    },
    search() {
      if (this.searchQuery) {
        this.searchResults = this.cars.filter(car => car.carName.includes(this.searchQuery)).map(car => ({
          id: car.carID,
          name: car.carName,
          price: car.carPrice,
          type: car.carType,
          description: car.carDis,
          owner: car.carMaster,
          status: car.carState,
          renter: car.renter
        }));
      } else {
        this.searchResults = this.cars.map(car => ({
          id: car.carID,
          name: car.carName,
          price: car.carPrice,
          type: car.carType,
          description: car.carDis,
          owner: car.carMaster,
          status: car.carState,
          renter: car.renter
        }));
      }
    },
    editItem(index) {
      this.currentItem = Object.assign({}, this.searchResults[index]);
      this.showModal = true;
    },
    closeModal() {
      this.showModal = false;
    },
    updateItem() {
      let url = `/car/modifyCar/${this.currentItem.id}`;
      let data = {
        carName: this.currentItem.name,
        rentalPrice: this.currentItem.price,
        carType: this.currentItem.type,
        carDescription: this.currentItem.description
      };

      axios.put(url, data)
        .then(response => {
          console.log('更新成功: ', response.data);
          // 在这里添加更新成功后的逻辑
          this.showModal = false;
          this.updateData(); // 更新列表
        })
        .catch(error => {
          console.error('更新失败: ', error);
          // 在这里添加更新失败后的逻辑
        });
    },
    deleteItem(index) {
      this.currentItem = index;
      this.showDeleteModal = true;
    },
    closeDeleteModal() {
      this.showDeleteModal = false;
    },
    confirmDelete() {
      let url = `/car/removeCar/${this.searchResults[this.currentItem].id}`;

      axios.delete(url)
        .then(response => {
          console.log('删除成功: ', response.data);
          // 在这里添加删除成功后的逻辑
          this.updateData();//更新
          this.showDeleteModal = false;
        })
        .catch(error => {
          console.error('删除失败: ', error);
          // 在这里添加删除失败后的逻辑
        });
    }
  }
};
</script>

<style scoped>
